import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Pergunta_auditoria } from '../../../modelo/Pergunta_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'pergunta_auditoria-dados-edit',   templateUrl: 'pergunta_auditoria_dadosEdit.component.html' }) export class Pergunta_auditoriaDadosEditComponent implements OnInit {    pergunta_auditoria: Pergunta_auditoria;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.pergunta_auditoria.Pergunta_auditoria";                 let id = +params['id'];         this.pergunta_auditoria = new Pergunta_auditoria();         this.pergunta_auditoria.cd_pergunta_auditoria = id;         o.objetoJson = this.pergunta_auditoria;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a pergunta_auditoria. ';       }      this.pergunta_auditoria = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


